header {
    width: 100%;
    height: 50px;
    background-color: #000;
    line-height: 50px;
    padding: 0 20px;

    .holder {
        width: 100%;
        height: 100%;
        max-width: 1200px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: center;

        a {
            color: #fff;
            text-decoration: none;
        }
    }
}


.customEditor .editor {
    padding: 1em !important;
    min-height: 100%;
}

@media screen and (min-width: 600px) {
    .customEditor .editor {
        padding: 2em !important;
    }
}

@media screen and (min-width: 800px) {
    .customEditor .editor {
        padding: 4em !important;
    }
}